<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                需求详情
            </p>
        </section>
        <section class="middle">
            <div class="figure">
                <img :src="detail.img" alt="">
            </div>
            <p class="article-title" v-text="detail.name"></p>
            <p class="time">
                <span><img width="18px" src="../../../assets/news/details_pageview_icon@2x.png">浏览量:{{ detail.reviews }}</span>
                <span class="line"></span>
                <span><img width="18px" src="../../../assets/technology/icon_Dockingquantity@2x.png" alt="">对接量:{{detail.dockingCount}}</span>
            </p>
            <p class="collect">
                <span v-if="detail.isCollection" @click="recollect"><img width="15px" src="../../../assets/technology/icon_Collected@2x.png"><span style="font-size: 14px">已收藏</span></span>
                <span v-else @click="collect"><img width="15px" src="../../../assets/technology/icon_Uncollected@2x.png"><span style="font-size: 14px">收藏</span></span>
            </p>
        </section>
        <hr>
        <section class="bottom">
            <div class="cover">
                <ul>
                    <li>需求领域:{{detail.genre}}</li>
                    <li>技术指标:{{detail.technicalIndicators}}</li>
                    <li>研发经费:{{detail.funding}}</li>
                    <li>研发周期:{{detail.cycleEnd - detail.cycleStart}}个月</li>
                    <li>资质要求:{{detail.qualifications}}</li>
                    <li>有效时限:{{detail.timeLimit}}</li>
                </ul>
            </div>
            <div class="title red-line">需求简介</div>
            <div class="article" v-html="detail.intro"></div>
            <div class="title blue-line">附件下载</div>
            <div class="link" @click="download" v-text="detail.fileName">文件名</div>
        </section>
        <div class="button" @click="docking">
            <img src="@/assets/home/icon_xuqiuduijie@2x.png" alt="" style="margin-top: -1px">
            <span>需求对接</span>
        </div>
        <md-dialog
                :closable="true"
                v-model="iconDialog.open"
                :btns="iconDialog.btns"
        >
            <img src="../../../assets/technology/img@2x.png" alt="">
            <p>对接信息已发送,</p>
            <p>工作人员将会在24小时内联系您!</p>
        </md-dialog>
    </main>
</template>
<script>
import { getDetail, docking, collect, recollect } from '@/apiUrl/technology'
import { recollectP } from "../../../apiUrl/activity";
import { Icon, Toast, ActivityIndicator, Landscape, Dialog } from 'mand-mobile'
export default {
  name: 'news_detail',
  components: {
    [Icon.name]: Icon,
    [ActivityIndicator.name]: ActivityIndicator,
    [Landscape.name]: Landscape,
    [Toast.name]: Toast,
    [Dialog.name]: Dialog
  },
  data () {
    return {
      showMaskClosable: false,
      currentId: '',
      detail: {},
      iconDialog: {
        open: false
      }
    }
  },
  mounted () {
    this.currentId = this.$route.query.id
    this.getDetail()
  },
  methods: {
    getDetail () {
      getDetail({ id: this.currentId }).then(res => {
        this.detail = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    // 回退
    goBack () {
      this.$router.go(-1)
    },
    download () {
      window.open(this.detail.file)
    },
    docking () {
        this.$router.push({ path: '/submission/index' ,query: { type : 2} })
      // docking({ type: 2, infoId: this.currentId }).then(res => {
      //   // this.showMaskClosable = true
      //   this.iconDialog.open = true
      // }).catch(err => {
      //   Toast.failed(err)
      // })
    },
      // 取消收藏
      recollect () {
          const args = {
              infoId: this.currentId,
              type: 2
          }
          recollectP(args).then(res => {
              Toast.succeed('收藏成功')
              this.getDetail()
              this.collected = true
          }).catch(err => {
              Toast.failed(err)
          })
      },
    // 收藏
    collect () {
      const args = {
        infoId: this.currentId,
        type: 2
      }
      collect(args).then(res => {
        Toast.succeed('收藏成功')
        this.getDetail()
        this.collected = true
      }).catch(err => {
        Toast.failed(err)
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        margin-bottom 0!important
        padding-top 88px
        .middle
            width 690px
            margin 20px auto
            .figure
                margin 30px auto 0
                img
                    width 690px
                    border-radius 8px
            .article-title
                font-size 32px
                color #333333
                text-align left
                line-height 48px
                margin-bottom 20px
            .time
                width 690px
                margin 0 auto
                display flex
                justify-content left
                font-size 24px
                span
                    display flex
                    align-items center
                    color #333333
                    img
                        margin-right 10px
                .line
                    margin 0 20px
                    color #999999
                    border-left 1px solid #999999
            .collect
                width 690px
                margin 36px auto 0
                display flex
                justify-content left
                font-size 28px
                span
                    display flex
                    align-items center
                    color #333333
                    img
                        margin-right 10px
        hr
            border none
            border-bottom 24px solid #EEEEEE
            margin 30px auto
        .bottom
            margin 0 auto 0
            padding-bottom 150px
            width 690px
            .cover
                background rgba(255, 255, 255, 1)
                box-shadow: 1px 1px 8px 0px rgba(2, 25, 85, 0.2)
                border-radius 6px
                padding 20px
                margin-bottom 40px
                ul
                    display flex
                    flex-wrap wrap
                    justify-content space-between
                    li
                        font-size 24px
                        color #666666
                        flex 0 0 50%
                        text-align left
                        margin 20px auto
                        padding-left 36px
                    li:nth-child(1)
                        background url("../../../assets/technology/item_red@2x.png") left center no-repeat
                        background-size 26px 26px
                    li:nth-child(4)
                        background url("../../../assets/technology/item_red@2x.png") left center no-repeat
                        background-size 26px 26px
                    li:nth-child(5)
                        background url("../../../assets/technology/item_red@2x.png") left center no-repeat
                        background-size 26px 26px
                    li:nth-child(2)
                        background url("../../../assets/technology/item_blue@2x.png") left center no-repeat
                        background-size 26px 26px
                    li:nth-child(3)
                        background url("../../../assets/technology/item_blue@2x.png") left center no-repeat
                        background-size 26px 26px
                    li:nth-child(6)
                        background url("../../../assets/technology/item_blue@2x.png") left center no-repeat
                        background-size 26px 26px
            .title
                width 690px
                height 66px
                line-height 66px
                color #333333
                font-size 28px
                text-indent 30px
                text-align left
                margin-bottom 30px
            .red-line
                background rgba(153, 153, 153, 0.1) url("../../../assets/technology/line_red@2x.png") left center no-repeat
                background-size 13px 66px
            .article
                width 690px
                padding 28px 32px
                font-size 24px
                color #666666
                border 2px solid rgba(153, 153, 153, 0.3)
                border-radius 8px
                line-height 36px
                text-align left
                margin-bottom 30px
            .blue-line
                background rgba(153, 153, 153, 0.1) url("../../../assets/technology/line_blue@2x.png") left center no-repeat
                background-size 13px 66px
            .link
                font-size 24px
                color #021955
                padding-left 18px
                text-align left
                margin-bottom 52px
        .button
            position fixed
            bottom 0
            display: flex
            align-items center
            justify-content center
            width 100%
            height 98px
            background rgba(229,232,238,1);
            font-size 32px
            box-shadow 0px 2px 12px 0px rgba(2,25,85,0.36);
            img
                width 26px
                height 42px
                margin-right 19px
    .md-landscape-content
        padding 30px
        font-size 30px
        line-height 1.5
        color #666
        p
            margin-bottom 30px
    /deep/.md-dialog-body
                display: flex;
                justify-content: center;
                flex-direction: column;
                align-items: center;
                text-align center
                img
                    width 80%
                p
                    margin 15px auto
</style>
